'use strict';

/**
* Regular expression to capture a POSIX filename extension.
*
* @module @stdlib/regexp-extname-posix
*
* @example
* import reExtnamePosix from '@stdlib/regexp-extname-posix' ;
*
* var RE_EXTNAME_POSIX = reExtnamePosix();
* var ext = RE_EXTNAME_POSIX.exec( 'index.js' )[ 1 ];
* // returns '.js'
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property' ;
import reExtnamePosix from './main.js' ;
import REGEXP from './regexp.js' ;


// MAIN //

setReadOnly( reExtnamePosix, 'REGEXP', REGEXP );


// EXPORTS //

export default reExtnamePosix;
